import Box from '@material-ui/core/Box';
import React, { Dispatch, lazy, SetStateAction, Suspense, useState } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import { ILog } from './interfaces/ILog';

import { repository } from './repositories/localStrage';

import { Header } from './components/molecules/header/Header';
import { Footer } from './components/molecules/footer/Footer';

/* pages */
const Tracker = lazy(() =>import('./pages/tracker/Tracker'));
const Policy = lazy(() => import('./pages/policy/Policy'));
const Settings = lazy(() => import('./pages/settings/Settings'));
const Logs = lazy(() => import('./pages/logs/Logs'));

function PageTemplate(props: { inner: () => JSX.Element }) {
    return <Box display="flex" flexDirection="column" justifyContent="space-between" height="100vh">
        <Header />
        <Box flexGrow="1">
            {props.inner()}
        </Box>
        <Footer />
    </Box>
}



export function Sink() {
    const [logs, setLogs]: [ILog[], Dispatch<SetStateAction<ILog[]>>] = useState(repository.getLogs)
    const [historyLimit, setHistoryLimit]: [number, Dispatch<SetStateAction<number>>] = useState(repository.getHistoryLimit())
    return (
        <BrowserRouter>
            <Suspense fallback={<PageTemplate inner={()=><div>Loading...</div>}/>}>
                <Switch>
                    <Route exact path="/sink/tracker" render={() => <PageTemplate inner={() => <Tracker repo={repository} setLogs={setLogs} logs={logs} historyLimit={historyLimit} />} />} />
                    <Route exact path="/sink/logs" render={() => <PageTemplate inner={() => <Logs logs={logs} />} />} />
                    <Route exact path="/sink/policy" render={() => <PageTemplate inner={() => <Policy />} />} />
                    <Route exact path="/sink/settings" render={() => <PageTemplate inner={() => <Settings setLogs={setLogs} repo={repository} historyLimit={historyLimit} setHistoryLimit={setHistoryLimit} />} />} />
                    <Route path="/sink/" render={() => <PageTemplate inner={() => <Tracker repo={repository} setLogs={setLogs} logs={logs} historyLimit={historyLimit} />} />} />
                </Switch>
            </Suspense>
        </BrowserRouter>
    );
}
